<template lang="en">
    <section>
    
        <div class="left-menu-tab">
            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"></dynamic-form>
        </div>
    
        <div class="right-content">
    
            <div id="ProductivityHeading" class="theme-primary partition  p-actions-expand"><span class="p-name text-white">{{navheading}}</span>
            </div>
            <Form ref="ProductivityForm">
                <div class="DivProductivity col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                    <div class="col-lg-12 mt-3 p-0">
                        <div class="col-sm-12 col-md-12 float-right col-lg-3 col-xl-3" v-if="SelectAllBox">
                            <div class="form-group">
                                <div class="row">
                                    <div class="col-12 float-left">
                                        <div class="input-group ml-3">
                                            <div class="custom-control custom-checkbox custom-check custom-control-inline">
                                                <input name="SelectAll"  v-model="isChecked" v-on:click="SelectAll()"   id="chkAll" type="checkbox" >
                                                <label for="chkAll" class="custom-control-label universal-custom-control-label pt-1"></label>Select All
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="heading-border mb-2">
                            <h5 class="h5">
                                <span>{{$t('ProductivityApps')}}</span>
                            </h5>
                        </div>
    
                    </div>
                    <div class="p-3">
                        <div :id="item.application_display_name" v-if="item.productive_type === 'Productive'" class="col-sm-12 col-md-12 float-left col-lg-3 col-xl-3" v-for="(item, index) in ApplicationData" :key="index">
    
                            <div class="form-group">
                                <div class="row">
                                    <div class="col-12 float-left">
                                        <div class="input-group">
                                            <label class="form-check form-check-inline">
                                                <a class="custom-control custom-check custom-control-inline">
                                                    <input type="checkbox" v-on:click="ShowDivClick()" :value="item.application_id" :name="item.application_display_name" :id="'chk_' + item.application_id" class="custom-control-input chkItems" />
                                                    <label class="custom-control-label universal-custom-control-label pt-1" :for="'chk_' + item.application_id">
                                                    </label>
                                                </a><em :class="item.icon_class ? 'appicon ' + item.icon_class : 'appicon default'"></em>&nbsp; {{item.application_display_name}}
                                            </label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
    
                        <div class="row col-12" v-if="showDiv">
                            <div class="col-2 col-sm-3 ml-4">
                                <label><span id="">{{ $t('SelectStatus') }}</span></label><span class="text-danger">*</span>
                                <Field name="Type" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <select name="Type" v-model="SelectedVal" :class="{'form-control w-100':true,'is-invalid':errors[0]}">
                                        <option value="">Select</option>
                                        <option value="Productive">Productive</option>
                                        <option value="UnProductive">UnProductive</option>
                                        <option value="Neutral">Neutral</option>
                                    </select>
                                    <span v-if="errors[0]" name="Type" class="invalid-feedback d-block">{{errors[0] }}</span>
                                </Field>
                            </div>
                            <div class="col-1 col-sm-2 mt-4">                            
                                <button class="form-control btn btn-success" type="button" @click="ConfirmChanges">Confirm</button>
                            </div>
                        </div>
                    </div>
    
                </div>
            </form>

            <Form ref="UnProductivityForm">
                <div class="DivUnProductivity col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                    <div class="col-lg-12 mt-3 p-0">
                        <div class="col-sm-12 col-md-12 float-right col-lg-3 col-xl-3" v-if="SelectAllBox1">
                            <div class="form-group">
                                <div class="row">
                                    <div class="col-12 float-left">
                                        <div class="input-group ml-3">
                                            <div class="custom-control custom-checkbox custom-check custom-control-inline">
                                                <input name="SelectAll1" v-model="isChecked1" v-on:click="SelectAll(1)"  id="chkAll1" type="checkbox" >
                                                <label for="chkAll1" class="custom-control-label universal-custom-control-label pt-1"></label>Select All
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="heading-border mb-2">
                            <h5 class="h5">
                                <span>{{$t('UnProductivityApps')}}</span>
                            </h5>
                        </div>
    
                    </div>
                    <div class="p-3">
                        <div :id="item.application_display_name" v-if="item.productive_type === 'UnProductive'" class="col-sm-12 col-md-12 float-left col-lg-3 col-xl-3" v-for="(item, index) in ApplicationData" :key="index">
                            <div class="form-group">
                                <div class="row">
                                    <div class="col-12 float-left">
                                        <div class="input-group">
                                            <label class="form-check form-check-inline">
                                                <a class="custom-control custom-check custom-control-inline">
                                                    <input type="checkbox" v-on:click="ShowDivClick(1)" :value="item.application_id" :name="item.application_display_name" :id="'chkb_' + item.application_id" class="custom-control-input chkItems1" />
                                                    <label class="custom-control-label universal-custom-control-label pt-1" :for="'chkb_' + item.application_id">
                                                    </label>
                                                </a><em :class="item.icon_class ? 'appicon ' + item.icon_class : 'appicon default'"></em>&nbsp; {{item.application_display_name}}
                                            </label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
    
                        <div class="row col-12" v-if="showDiv1">
                            <div class="col-2 col-sm-3 ml-4">
                                <label><span id="">{{ $t('SelectStatus') }}</span></label><span class="text-danger">*</span>
                                <Field name="Type" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <select name="Type" v-model="SelectedVal1" :class="{'form-control w-100':true,'is-invalid':errors[0]}">
                                        <option value="">Select</option>
                                        <option value="Productive">Productive</option>
                                        <option value="UnProductive">UnProductive</option>
                                        <option value="Neutral">Neutral</option>
                                    </select>
                                    <span v-if="errors[0]" name="Type" class="invalid-feedback d-block">{{errors[0] }}</span>
                                </Field>
                            </div>
                            <div class="col-1 col-sm-2 mt-4"> 
                                <button class="form-control btn btn-success" type="button" @click="ConfirmChanges1">Confirm</button>
                            </div>
                        </div>
                    </div>
    
                </div>
            </form>

            <Form ref="NeutralForm">
                <div class="DivNeutral col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                    <div class="col-lg-12 mt-3 p-0">
                        <div id="SelectAll2" class="col-sm-12 col-md-12 float-right col-lg-3 col-xl-3" v-if="SelectAllBox2">
                            <div class="form-group">
                                <div class="row">
                                    <div class="col-12 float-left">
                                        <div class="input-group ml-3">
                                            <div class="custom-control custom-checkbox custom-check custom-control-inline">
                                                <input name="SelectAll2" v-model="isChecked2" v-on:click="SelectAll(2)"  id="chkAll2" type="checkbox" >
                                                <label for="chkAll2" class="custom-control-label universal-custom-control-label pt-1"></label>Select All
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="heading-border mb-2">
                            <h5 class="h5">
                                <span>{{$t('NeutralApps')}}</span>
                            </h5>
                        </div>
    
                    </div>
                    <div class="p-3">
                        <div :id="item.application_display_name" v-if="item.productive_type === 'Neutral'" class="col-sm-12 col-md-12 float-left col-lg-3 col-xl-3" v-for="(item, index) in ApplicationData" :key="index">
                            <div class="form-group">
                                <div class="row">
                                    <div class="col-12 float-left">
                                        <div class="input-group">
                                            <label class="form-check form-check-inline">
                                                <a class="custom-control custom-check custom-control-inline">
                                                    <input type="checkbox" v-on:click="ShowDivClick(2)" :value="item.application_id" :name="item.application_display_name" :id="'chkbox_' + item.application_id" class="custom-control-input chkItems2" />
                                                    <label class="custom-control-label universal-custom-control-label pt-1" :for="'chkbox_' + item.application_id">
    
                                                    </label>
                                                </a><em :class="item.icon_class ? 'appicon ' + item.icon_class : 'appicon default'"></em>&nbsp; {{item.application_display_name}}
                                            </label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
    
                        <div class="row col-12" v-if="showDiv2">
                            <div class="col-2 col-sm-3 ml-4">
                                <label><span id="">{{ $t('SelectStatus') }}</span></label><span class="text-danger">*</span>
                                <Field name="Type" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <select name="Type" v-model="SelectedVal2" :class="{'form-control w-100':true,'is-invalid':errors[0]}">
                                        <option value="">Select</option>
                                        <option value="Productive">Productive</option>
                                        <option value="UnProductive">UnProductive</option>
                                        <option value="Neutral">Neutral</option>
                                    </select>
                                    <span v-if="errors[0]" name="Type" class="invalid-feedback d-block">{{errors[0] }}</span>
                                </Field>
                            </div>
                            <div class="col-1 col-sm-2 mt-4"> 
                                <button class="form-control btn btn-success" type="button" @click="ConfirmChanges2">Confirm</button>
                            </div>
                        </div>
                    </div>
    
                </div>
            </form>
    
        </div>
    
    </section>
    </template>
    
    <script>
    import DataService from '../../services/DataService';
    import validationRules from '../../vee-validate';
    export default {
        comments:{
            validationRules
        },
        data() {
            return {
                ApplicationData: [],
                SelectedVal: '',
                SelectedVal1: '',
                SelectedVal2: '',                
                showDiv:false,
                showDiv1:false,
                showDiv2:false,
                isChecked:false,
                isChecked1:false,
                isChecked2:false,
                SelectAllBox:false,
                SelectAllBox1:false,
                SelectAllBox2:false,
                navheading: 'User Productivity',
                FormSchema: [{
                        layoutType: "single",
                        Data: [{
                                astype: "SelectField",
                                label: this.$t('SelectUser'),
                                name: "User",
                                value: "",
                                placeholder: "",
                                visibility: true,
                                validationRules: {
                                    "required": true
                                },
                                config: {
                                    options: [],
                                    onChange: this.FetchData
                                }
                            },
    
                        ]
                    },
    
                ],
                buttons: [],
            }
        },
        mounted() {
            var vm = this;
            setTimeout(function () {
            vm.CheckBoxBootstrap();
            vm.ResponsiveDataTable('tablelistingdata');               
            }, 100);
        },
        created: async function () {            
            var vm = this;
            vm.FetchData();      
            await vm.GetCompanyUsers();
        },
        methods: {
            GetCompanyUsers: async function () {
                var vm = this;
                (vm.FormSchema)[0].Data[0].config.options = [];
                vm.isLoading = true;
                await DataService.GetUsersByCompanyId().then(response => {
                    vm.UsersData = response.data;
                    response.data.forEach((item) => {
                        (vm.FormSchema)[0].Data[0].config.options.push({
                            name: `${item.username}`,
                            value: `${item.user_id}`,
                        })
                        vm.isLoading = false;
                    })
                    if (response.data.length <= 0) {
                        (vm.FormSchema)[0].Data[0].config.options = [];
                        vm.isLoading = false;
                    }
                });
            },
            
            FetchData:function() {       
                var vm = this;
                vm.isLoading = true;
                var url = '';
                if((vm.FormSchema)[0].Data[0].value==null||(vm.FormSchema)[0].Data[0].value==''){                    
                    url='userid=0'
                }else{
                    url='userid='+(vm.FormSchema)[0].Data[0].value;
                }                
                DataService.GetAllApp(url).then(response => {                    
                    if (response.data.length > 0) {
                        vm.ApplicationData = response.data;                        
                        vm.isLoading = false;                        
                    }else{
                        vm.ApplicationData = [];                        
                        vm.isLoading = false;
                    }
                        vm.showDiv=false;
                        vm.showDiv1=false;
                        vm.showDiv2=false; 
                        vm.isChecked=false;                    
                        vm.isChecked1=false;                    
                        vm.isChecked2=false;
                      setTimeout(function () {
                         vm.CheckBoxBootstrap();
                        $('.chkItems:checkbox:not("#chkAll")').prop('checked', false);
                        $('.chkItems1:checkbox:not("#chkAll")').prop('checked', false);
                        $('.chkItems2:checkbox:not("#chkAll")').prop('checked', false);
                        
                     }, 250); 
                     vm.SelectAllBox=false;
                     vm.SelectAllBox1=false;
                     vm.SelectAllBox2=false;
                     vm.ApplicationData.forEach(item=>{                        
                        if(item.productive_type === 'Productive'){
                            vm.SelectAllBox=true;
                        }else if(item.productive_type === 'UnProductive'){
                            vm.SelectAllBox1=true;
                        }else if(item.productive_type === 'Neutral'){
                            vm.SelectAllBox2=true;
                        }
                     })                 
                });                
            },

            ShowDivClick:function(id){ 
                               
                var val='';
                if(id=='undefined'||id==''||id==null){
                        $(`.chkItems:checkbox:checked`).each(function () {
                        if (val.length > 0)
                            val += ",";
                        val += $(this).val();
                    });                    
                }else{
                    $(`.chkItems${id}:checkbox:checked`).each(function () {
                        if (val.length > 0)
                            val += ",";
                        val += $(this).val();
                    });                    
                } 
                if(val){
                        if(id=='undefined'||id==''||id==null){
                            this.showDiv=true;                           
                            this.showDiv1=false;
                            this.showDiv2=false;
                            $('.chkItems1:checkbox:not("#chkAll")').prop('checked', false);
                            $('.chkItems2:checkbox:not("#chkAll")').prop('checked', false);
                        }else if(id==1){
                            this.showDiv1=true;
                            this.showDiv=false;                       
                            this.showDiv2=false;
                            $('.chkItems:checkbox:not("#chkAll")').prop('checked', false);
                            $('.chkItems2:checkbox:not("#chkAll")').prop('checked', false);
                        }else{
                            this.showDiv2=true;
                            this.showDiv=false;
                            this.showDiv1=false;
                            $('.chkItems:checkbox:not("#chkAll")').prop('checked', false);
                            $('.chkItems1:checkbox:not("#chkAll")').prop('checked', false);
                        }                        
                    }else{
                        this.showDiv=false;
                        this.showDiv1=false;
                        this.showDiv2=false;
                    }               
                
            },
    
            SelectAll: function (id) {
                              
                if(id=='undefined'||id==''||id==null){
                    if (!this.isChecked) {                            
                        $('.chkItems:checkbox:not("#chkAll")').prop('checked', true);                       
                    } else {
                        $('.chkItems:checkbox:not("#chkAll")').prop('checked', false);
                        
                    }
                    this.isChecked1=false;                    
                    this.isChecked2=false;                    
                 }
                else if(id==1){                        
                     if (!this.isChecked1) {
                        $(`.chkItems${id}:checkbox:not("#chkAll${id}")`).prop('checked', true);                        
                    } else {
                        $(`.chkItems${id}:checkbox:not("#chkAll${id}")`).prop('checked', false);                        
                    }
                    this.isChecked=false;                    
                    this.isChecked2=false; 
                }
                else{                        
                     if (!this.isChecked2) {
                        $(`.chkItems${id}:checkbox:not("#chkAll${id}")`).prop('checked', true);                       
                    } else {
                        $(`.chkItems${id}:checkbox:not("#chkAll${id}")`).prop('checked', false);                       
                    }
                    this.isChecked1=false;                    
                    this.isChecked=false; 
                }              
                this.ShowDivClick(id);
            },           
            ConfirmChanges() {
                var vm = this;
                var val = "";
                
                if((vm.FormSchema)[0].Data[0].value==null||(vm.FormSchema)[0].Data[0].value==''){
                    $('[name="User"]').focus();
                    $('[name="User"]').blur();
                }                
                $(".chkItems:checkbox:checked").each(function () {
                    if (val.length > 0)
                        val += ",";
                    val += $(this).val();
                });
                this.$refs.ProductivityForm.validate().then(result => {
                    if (result) {            
                        if (val) {
                            if (vm.SelectedVal && (vm.FormSchema)[0].Data[0].value) {
                                vm.UpdateProductiveType(val, vm.SelectedVal, (vm.FormSchema)[0].Data[0].value)
                            } else {
                                vm.FetchData();
                                vm.isLoading = false;
                            }                     
                        }
                    }
                });
    
            },
            ConfirmChanges1() {
                
                var vm = this;
                var val = ""
                if((vm.FormSchema)[0].Data[0].value==null||(vm.FormSchema)[0].Data[0].value==''){ 
                 $('[name="User"]').focus();
                 $('[name="User"]').blur();
                }
                $(".chkItems1:checkbox:checked").each(function () {
                    if (val.length > 0)
                        val += ",";
                    val += $(this).val();
                });
                this.$refs.UnProductivityForm.validate().then(result => {
                    if (result) {            
                        if (val) {
                            if (vm.SelectedVal1 && (vm.FormSchema)[0].Data[0].value) {
                                vm.UpdateProductiveType(val, vm.SelectedVal1, (vm.FormSchema)[0].Data[0].value)
                            } else {
                                vm.FetchData();
                                vm.isLoading = false;
                            }                   
                        }
                    }
                });
            },
            ConfirmChanges2() {
                var vm = this;
                var val = ""
                
                if((vm.FormSchema)[0].Data[0].value==null||(vm.FormSchema)[0].Data[0].value==''){ 
                    $('[name="User"]').focus();
                    $('[name="User"]').blur();
                }              
                $(".chkItems2:checkbox:checked").each(function () {
                    if (val.length > 0)
                        val += ",";
                    val += $(this).val();
                });
                this.$refs.NeutralForm.validate().then(result => {
                    if (result) {            
                        if (val) {
                            if (vm.SelectedVal2 && (vm.FormSchema)[0].Data[0].value) {
                                vm.UpdateProductiveType(val, vm.SelectedVal2, (vm.FormSchema)[0].Data[0].value)
                            } else {
                                vm.FetchData();
                                vm.isLoading = false;
                            }
                            
                        }
                    }
                });
    
            },
    
            UpdateProductiveType(val, Dropval, Userid) {
                var vm = this;
                var url = `Ids=${val}&Type=${Dropval}&selectedUserId=${Userid}`
                DataService.UpdateProductiveType(url).then(response => {
                    
                    if (response.data.length > 0) {
                        vm.ShowAlert(vm.$t('AppTypeSuccessfully'), "success", true, vm.$t('Alert'));
                        vm.FetchData();
                        vm.isLoading = false;
                    } else {
                        vm.FetchData();
                        vm.isLoading = false;
                    }
                });
            }
    
        },
    
    }
    </script>   
    

  
    